import * as tableActions from '../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { mainWindow } from '../App'

const width = mainWindow.current.offsetWidth>800? 500: 240;
const width1 = mainWindow.current.offsetWidth>800? 240: 120;
const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    textFieldSmall: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width1
        },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    message: {
        width: width,
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: 'calc((100% - '+width+'px)/2)',
        marginRight: 'calc((100% - '+width+'px)/2)'
    },
});

const Event = React.memo(
    (props) =>{
        const { classes } = props;
        let getElsomCode = async (wallet)=>{
            let data = await getElsom({wallet: wallet})
            if(data==undefined)
                data = 'error'
            setStatus(data)
        }
        let [wallet, setWallet] = useState('');
        let handlewallet = (event) => {
            setWallet(event.target.value)
        };
        let [status, setStatus] = useState('');
        return (
            <div>
                <TextField
                    label='кошелек'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={wallet}
                    onChange={handlewallet}
                />
                <br/>
                <b>Статус: {status}</b>
                <br/>
                <Button variant='contained' color='primary' onClick={()=>{getElsomCode(wallet)}} className={classes.button}>
                    Узнать статус
                </Button>
            </div>
        );
  })

function mapStateToProps (state) {
    return {
        user: state.user,
        table: state.table,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Event.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Event));
